.dataTables_wrapper .dataTables_length label {
  display: flex;
  align-items: center;
  column-gap: 4px;
}
.dataTables_wrapper .dataTable thead tr th {
  background-color: #222;
  color: #fff;
  transition: all 0.5s;
}
.dataTables_wrapper .dataTable thead tr th:hover {
  background-color: #333;
}
.dataTables_wrapper .dataTable thead tr th::before, .dataTables_wrapper .dataTable thead tr th:after {
  color: #fff !important;
  opacity: 0.6 !important;
}
.dataTables_wrapper .dataTable thead tr th.sorting_asc, .dataTables_wrapper .dataTable thead tr th.sorting_desc {
  background-color: #000;
}
.dataTables_wrapper .dataTable thead tr th.sorting_asc::before {
  opacity: 0.9 !important;
}
.dataTables_wrapper .dataTable thead tr th.sorting_desc::after {
  opacity: 0.9 !important;
}
.dataTables_wrapper .dataTable tbody .Activo, .dataTables_wrapper .dataTable tbody .Activa {
  color: #30bf30;
  font-weight: 700;
}
.dataTables_wrapper .dataTable tbody .Inactivo, .dataTables_wrapper .dataTable tbody .Inactiva {
  color: red;
  font-weight: 700;
}
.dataTables_wrapper .dataTable tbody .acciones .accion {
  background-color: #0095e8;
  color: #fff;
  font-size: 13px;
  text-transform: uppercase;
  padding: 8px 18px;
  line-height: 1;
  font-weight: 600;
  border: none;
  transition: all 0.5s;
}
.dataTables_wrapper .dataTable tbody .acciones .accion:hover {
  background-color: #00d6b0;
  color: #2d2d2d;
}

#wrap-vehiculos {
  display: flex;
  flex-wrap: wrap;
  row-gap: 20px;
}
#wrap-vehiculos .wrap-listado-vehiculos,
#wrap-vehiculos .wrap-gestion-vehiculos {
  width: 100%;
}
#wrap-vehiculos .wrap-title {
  background-color: #0095e8;
  box-shadow: 0 0 7px 1px rgba(0, 0, 0, 0.32);
}
#wrap-vehiculos .wrap-title .title {
  margin: 0;
  line-height: 1;
  color: #fff;
  padding: 15px 20px;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-size: 22px;
  font-weight: 700;
  text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.45);
}
#wrap-vehiculos form {
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px;
  row-gap: 25px;
}
#wrap-vehiculos form .wrap {
  width: 100%;
  position: relative;
}
#wrap-vehiculos form .wrap label {
  text-shadow: none;
  margin-bottom: 5px;
}
#wrap-vehiculos form .wrap a {
  color: #086ACF;
  text-decoration: underline;
  transition: all 0.5s;
}
#wrap-vehiculos form .wrap a:hover {
  color: #00b9d9;
}
#wrap-vehiculos form .wrap .select2-container .select2-selection--single {
  height: 42px;
}
#wrap-vehiculos form .wrap .select2-container .select2-selection--single .select2-selection__rendered {
  line-height: 42px;
}
#wrap-vehiculos form .wrap.wrap-2 {
  width: 49%;
}